<template>
  <div class="home">
    <editor></editor>
    <div class="pin-list-view">
      <ul class="pin-list">
        <li
        class="pin-list-item shadow"
        v-for="(item, index) in list"
        :key="index"
        >
          <pin :item="item"></pin>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import editor from '../components/editor'
import pin from '../components/pin'
import { getArtical } from '../api/index'
export default {
  name: 'home',
  data(){
    return {
      list:[],
    }
  },
  components:{
    editor,
    pin
  },
  mounted(){
    getArtical(1).then(res => {
      if (res.code === 200) {
        this.list = res.data
      }
    })
  }
}
</script>
<style lang="stylus" scoped>
.banner
  height 100px
.pin-list
  position relative
  .pin-list-item
    margin-bottom 8px
</style>