<template>
    <div class="editor">
        <div class="editor-mask" v-show="!login_status">
            <button class="login-btn" @click="onLogin">登录</button>
        </div>
        <div class="editor-body">
            <div :class="['content',editorOn?'active':'']">
                <div :class="['rich-editor',isEmpty?'empty':'',images.length>0?'indent':'']"
                ref="editor"
                contenteditable="true"
                :tabindex="1"
                @input="editorChange"
                @focus="focus(true)"
                @blur="focus(false)"
                placeholder="告诉你个小秘密，添加话题会被更多小伙伴看见呦~"
                >
                </div>
                <div :class="['image-box']" v-if="images.length>0">
                    <div
                    :key="index"
                    class="preview-image"
                    v-for="(url,index) in images">
                        <div
                        class="loaded-image"
                        :style="{backgroundImage:`url(http://localhost:3000/upload/${url})`}"
                        ></div>
                        <span
                        class="delete"
                        @click="del(index)"
                        ></span>
                    </div>
                    <div
                    class="select-box"
                    @click="uploadImg"
                    v-if="images.length<9"
                    ></div>
                </div>
                <div :class="['url-box']" v-if="url_content_show">
                    <div class="preview-link">
                        <div class="cont">
                            <span class="title">{{parseUrl.title}}</span>
                            <span class="url">{{parseUrl.url}}</span>
                        </div>
                        <div class="thumb">
                            <img
                            style="width:100%;height:100%;"
                            src="../assets/question.png" alt="">
                        </div>
                        <span
                        class="close"
                        @click="clearparseUrl"
                        ></span>
                    </div>
                </div>
            </div>
        </div>
        <div class="editor-buttom">
            <div class="tool">
                <div class="picker">
                    <div class="picker-box" @click.stop="emoji_selector_show = true">
                        <img src="https://b-gold-cdn.xitu.io/v3/static/img/emoji.5594dbb.svg" alt="">
                        <span>表情</span>
                    </div>
                    <div class="emoji-selector"
                    @click.stop="emoji_selector_show = true"
                    v-show="emoji_selector_show">
                        <div class="triangle"></div>
                        <div class="emoji-content">
                            <ul class="category">
                                <li 
                                v-for="(item,index) in emojiPanel"
                                :key="index"
                                @click.stop="insertEmoji(item)"
                                class="item">
                                    <img 
                                    class="emoji"
                                    draggable="false"
                                    :src="item.url"
                                    :alt="item.emoji">
                                </li>
                            </ul>
                        </div>
                        <ul class="nextpage">
                            <li :class="[emoji_page ===1?'active':'']"
                            @click="changeEmjPage(1)"
                            ></li>
                            <li :class="[emoji_page ===2?'active':'']"
                            @click="changeEmjPage(2)"
                            ></li>
                            <li :class="[emoji_page ===3?'active':'']"
                            @click="changeEmjPage(3)"
                            ></li>
                        </ul>
                    </div>
                </div>
                <div class="picker">
                    <div :class="['picker-box',parseUrl.url?'not-allow':'']"
                    @click="uploadImg"
                    >
                    <img src="https://b-gold-cdn.xitu.io/v3/static/img/active_file.d265f4e.svg" alt="">
                    <span>图片</span>
                    </div>
                </div>
                <div class="picker">
                    <div :class="['picker-box',images.length>0?'not-allow':'']"
                    @click.stop="urlSelector"
                    ><img src="https://b-gold-cdn.xitu.io/v3/static/img/active_link.b1a6832.svg" alt="">
                        <span>链接</span>
                    </div>
                    <div class="linkPanel"
                    @click.stop="url_selector_show = true"
                    v-show="url_selector_show"
                    >
                        <div class="triangle"></div>
                        <div class="input">
                            <input
                            type="text"
                            placeholder="输入网址链接"
                            v-model="urlholder"
                            >
                        </div>
                        <div class="tip">
                            <span>自动抓取网页链接的标题和头图</span>
                            <span
                            :class="['add',urlholder?'active':'']"
                            @click="insertUrl"
                            >添加</span>
                        </div>
                    </div>
                </div>
                <div class="picker">
                    <div class="picker-box"><img src="https://b-gold-cdn.xitu.io/v3/static/img/topic.6a87bb7.svg" alt="">
                    <span>话题</span>
                    </div>
                </div>
            </div>
            <div class="submit">
                <div class="tip">Ctrl or ⌘ + Enter</div>
                <button :class="['btn',isEmpty?'not-allow':'']"
                @click="Submit"
                >发布</button>
            </div>
        </div>
    </div>
</template>

<script>
import emoji from './emoji'
import { upload, getUrlParse,addCommit } from '../api'
import { mapState } from 'vuex'
export default {
    name: "editor",
    data(){
        return{
            content: "",
            images: [],
            parseUrl: {
                title:'',
                url:'',
                thumb:''
            },
            editorOn:false,
            urlholder: '',
            emoji:emoji,
            emoji_page:1,
            emoji_selector_show: false,
            url_selector_show: false,
            url_content_show:false,
        }
    },
    mounted(){
        window.addEventListener('click', function() {
            this.emoji_selector_show = false
            this.url_selector_show = false
        }.bind(this),false)
    },
    beforeDestroy(){
        window.removeEventListener('click', function() {
            this.emoji_selector_show = false
            this.url_selector_show = false
        }.bind(this),false)
    },
    computed:{
        ...mapState('login',['login_status']),
        isEmpty(){
            return this.content.length>0?false:true
        },
        emojiPanel(){
            const start = (this.emoji_page-1)*54;
            const end = this.emoji_page*54;
            const list = this.emoji.filter((item,index) => {
                if (index>=start&&index<end){
                    return item
                }
            })
            return list
        }
    },
    methods:{
        editorChange(e){
            const val = e.target.innerHTML
            this.content = val
        },
        focus(val){
            this.editorOn = val
        },
        changeEmjPage(val){
            this.emoji_page = val
        },
        insertEmoji(emoji){
            this.$refs.editor.focus()
            let selection = window.getSelection()
            // 获取包含当前节点的文档片段
            let range = selection.getRangeAt(0)
            // 创建需追加到光标处节点的文档片段
            let emojiText = document.createElement('img');
                            emojiText.className = 'emoji';
                            emojiText.draggable = false;
                            emojiText.alt = emoji.emoji;
                            emojiText.src = emoji.url;
                            emojiText.style.width = '16px'
                            emojiText.style.height = '16px'
            // 将创建的文档片段插入到光标处
            range.insertNode(emojiText);
            this.content = this.$refs.editor.innerHTML
        },
        uploadImg(){
            if (this.images.length>8) {
                return
            }
            if (this.parseUrl.url) {
                return 
            }
            const input = document.createElement('input');
            input.type = 'file';
            input.onchange = function(e) {
                if (input.files[0].size>(1024*1024*2)){
                    // (1024*1024*2) 超过2M的图不能上传
                    return 
                }
                if (!input.files[0].type.includes('image')){
                    // 不是图片文件 不上传
                    return
                }
                upload(input.files[0])
                .then(res=>{
                    if (res.data.code === 200) {
                        this.images.push(res.data.file)
                    }
                })
            }.bind(this);
            input.click()
        },
        del(index){
            this.images.splice(index,1)
        },
        onLogin(){
            this.event.$emit('sign-in')
        },
        urlSelector(){
            if (this.images.length>0){
                return
            }
            this.url_selector_show = true
        },
        insertUrl(){
            let url = this.urlholder;
            if (url){
                getUrlParse(url)
                .then(res => {
                    if (res.code === 200) {
                        this.parseUrl = res.data
                        this.url_selector_show = false 
                        this.url_content_show = true
                        this.urlholder = ''
                    }
                })
            }
        },
        clearparseUrl(){
            this.url_content_show = false
            this.parseUrl = {
                title:'',
                url:'',
                thumb:''
            }

        },
        Submit(){
            if (!this.isEmpty){
                let {content,images,parseUrl} = this;
                console.log({content,images,parseUrl})
                addCommit({content,images,parseUrl})
                .then(res=>{
                    if (res.code === 200){
                        console.log(res);
                        this.content = '';
                        this.images = [];
                        this.parseUrl = {
                            title:'',
                            url:'',
                            thumb:''
                        }
                        this.url_content_show = false;
                        this.$refs.editor.innerHTML = ''
                    }
                })
            }
        }
    }
}
</script>

<style lang="stylus">
.not-allow 
    cursor not-allowed
    opacity .2
.editor 
    margin-bottom: 8px
    background-color: #fff;
    position: relative;
    border-radius: .2rem;
    box-shadow: 0 1px 2px 0 rgba(0,0,0,.05);
    &-body
        padding: 1.333rem 1.666rem 0;
        background: #fff;
        position: relative;
        border-radius: 2px;
        .content 
            position: relative;
            border-radius: 2px;
            border: 1px solid hsla(0,0%,59.2%,.2);
            background-color: rgba(226,230,235,.2);
            &.active 
                background-color #fff
                border 1px solid #007fff
            .rich-editor
                position: relative;
                height: 100%;
                outline: none;
                border-radius: 2px;
                color: #17181a;
                min-height: 75px;
                font-size: 12px;
                padding: 8px 10px;
                .emoji 
                    vertical-align: sub;
                    width: 15px;
                    height: 15px;
                    cursor: default;
                    margin: 0 2px;
                &.empty:after
                    display inline-block
                &:after 
                    content: attr(placeholder);
                    position: absolute;
                    top: 8px;
                    left 10px;
                    font-size 12px
                    color: rgba(23,24,26,.4);
                    pointer-events: none;
                    user-select: none;
                    display: none; 
                &.indent 
                    min-height: 39px;
        .image-box 
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;
            padding: 12px;
            position: relative;
            .select-box 
                cursor: pointer;
                width: 90px;
                height: 90px;
                position: relative;
                border-radius: 1.4px;
                border: 1px dashed #c5c5c5;
                background: #f8f8f9;
                &:before,&:after 
                    content: "";
                    width: 2rem;
                    height: 1px;
                    display: block;
                    position: absolute;
                    background: #c5c5c5;
                    left: 50%;
                    top: 50%;
                    transform: translate(-50%,-50%);
                &:after 
                    transform: translate(-50%,-50%) rotate(90deg);
            .preview-image 
                position: relative;
                cursor: pointer;
                .loaded-image 
                    border-radius: 2px;
                    margin: 0 8px 8px 0;
                    width: 90px;
                    height: 90px;
                    background-repeat: no-repeat;
                    background-size: cover;
                    flex: 0 0 auto;
                    background-position: 50%;
                    &:hover 
                        filter brightness(.8)
                .delete 
                    cursor: pointer;
                    width: 17px;
                    height: 17px;
                    position: absolute;
                    top: 4px;
                    right: 13px;
                    border-radius: 50%;
                    border: 1px solid #c5c5c5;
                    background: rgba(0,0,0,.4);
                    transform: rotate(45deg);
                    &:before,&:after
                        content: "";
                        width: 10px;
                        height: 1px;
                        display: block;
                        position: absolute;
                        background: #fff;
                        left: 50%;
                        top: 50%;
                        transform: translate(-50%,-50%);
                    &:after 
                        transform: translate(-50%,-50%) rotate(90deg);
        .url-box
            display: flex;
            flex-direction: row;
            border-radius: 4px;
            padding: 12px;
            .preview-link
                position: relative;
                box-sizing content-box
                padding: 11px 18px;
                border: 1px solid #ebebeb;
                border-radius: 4.44px;
                width: 462px;
                display: flex;
                justify-content: space-between;
                .thumb
                    position relative
                    width 72px 
                    height 72px
                .cont 
                    flex 1
                    display flex 
                    flex-direction column
                    justify-content space-between
                    .title 
                        font-size 16px
                        display: -webkit-box;
                        -webkit-line-clamp: 2;
                        -webkit-box-orient: vertical;
                        overflow: hidden;
                    .url 
                        font-size 12px
                        color #ccc
                .close
                    cursor: pointer;
                    width: 20px;
                    height: 20px;
                    position: absolute;
                    top:-10px;
                    right:-10px;
                    border-radius: 50%;
                    border: 1px solid #fff;
                    background: #000;
                    opacity: .65;
                    transform: rotate(45deg);
                    &:before,&:after 
                        content: "";
                        width: 1rem; 
                        height: 1px;
                        display: block;
                        position: absolute;
                        background: #c5c5c5;
                        left: 50%;
                        top: 50%;
                        transform: translate(-50%,-50%);
                    &:after 
                        transform: translate(-50%,-50%) rotate(90deg);
    &-buttom
        padding: .833rem 1.666rem;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        margin-top: -2px;
        .tool
            display flex
            align-items: center
            .picker
                position relative
                margin-right 20px
                display flex
                line-height normal
                user-select none
                .linkPanel
                    box-sizing content-box
                    padding: 16px 20px;
                    color: #aeb6c0;
                    background: #fff;
                    position: absolute;
                    width: 279px;
                    height: 65px;
                    top: 2.8rem;
                    left: -10rem;
                    bottom: 0;
                    border-radius: 2px;
                    box-shadow: 0 5px 18px 0 rgba(0,0,0,.16);
                    .triangle
                        position: absolute;
                        top: -.6rem;
                        left: 50%;
                        width: 0;
                        height: 0;
                        transform: translate(-50%,-50%);
                        border: .8rem solid transparent;
                        border-bottom-color: #fff;
                    .input  
                        position relative
                        input 
                            width: 100%;
                            background-color: #fafafb;
                            border: 1px solid #e5e5e5;
                            border-radius: 2px;
                            outline: #027fff;
                            color: #666;
                            font-size: 1rem;
                            padding: .5rem 2.5rem .5rem .666rem;
                            transition .3s
                            &::placeholder
                                color #ccc
                            &:focus 
                                outline: none;
                                border: 1px solid #027fff;
                                background-color: #fff;
                    .tip
                        padding: 13px 0;
                        font-size: 13px;
                        display: flex;
                        color: #17181a;
                        font-weight: 400;
                        justify-content: space-between;
                        span 
                            color #17181a
                            opacity .2
                        .add 
                            cursor not-allowed
                            &.active 
                                cursor pointer
                                color #007fff
                                opacity 1
                .emoji-selector
                    padding: 1rem;
                    position: absolute;
                    top: 2.8rem;
                    z-index: 1;
                    bottom: 0;
                    width: 307px;
                    height: 20rem;
                    border-radius: 2px;
                    background-color: #fff;
                    box-shadow: 0 5px 18px 0 rgba(0,0,0,.16);
                    .triangle
                        position: absolute;
                        top: -.7rem;
                        left: 15%;
                        width: 0;
                        height: 0;
                        transform: translate(-50%,-50%);
                        border: .8rem solid transparent;
                        border-bottom-color: #fff;
                    .emoji-content 
                        overflow: auto;
                        margin-bottom: 10px;
                        .category 
                            max-width: 280px;
                            max-height: 225px;
                            display: flex;
                            flex-wrap: wrap;
                            align-items: center;
                            justify-content: flex-start;
                            .item 
                                font-size: 1.4rem;
                                padding: 5px 4.5px;
                                cursor: pointer;
                                img.emoji
                                    width: 22px;
                                    height: 22px;
                                    display: block;
                    .nextpage
                        position: absolute;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        bottom: 10px;
                        left: 50%;
                        transform: translate(-50%,-50%);
                        li 
                            margin: 0 5px;
                            width: 6px;
                            height: 6px;
                            border-radius: 100%;
                            background-color: #f0f0f0;
                            cursor: pointer;
                            &.active 
                                background-color: #d8d8d8;
                &-box 
                    display flex
                    align-items center
                    cursor pointer
                img 
                    width 18px 
                    height 18px
                span 
                    font-size 13px
                    color #007fff
        .submit
            display flex
            .tip 
                color: #c2c2c2;
                text-align: center;
                width: 10rem;
                line-height: 32px;
                font-size: 13px;
                user-select: none;
            .btn
                -webkit-appearance: none;
                appearance: none;
                background-color: #007fff;
                color: #fff;
                border-radius: 2px;
                border: none;
                padding: .5rem 1.3rem;
                outline: none;
                transition: background-color .3s,color .3s;
                cursor: pointer;
                height: 32px;
                width: 6rem;
                font-size: 15px;
                &.not-allow 
                    cursor not-allowed
                    opacity .2
    &-mask 
        width 100% 
        height 100% 
        position absolute
        background-color rgba(255,255,255,0.7)
        z-index 9
        .login-btn
            -webkit-appearance: none;
            appearance: none;
            background-color: #007fff;
            color: #fff;
            border-radius: 2px;
            border: none;
            padding: .5rem 1.3rem;
            outline: none;
            transition: background-color .3s,color .3s;
            cursor: pointer;
            height: 32px;
            width: 6rem;
            font-size: 15px;
            position absolute
            top 50% 
            left 50%
            transform translate(-50%,-50%)
</style>
