<template>
<transition name="fade">
  <div class="login container" v-if="show">
      <div class="login-modal">
          <div
          class="login-modal_close"
          @click="show = false"
          ></div>
          <h1 style="text-align:center;user-select:none;font-size:18px;">
              <a :class="[type ===1?'active':'']"
              @click="changeType(1)"
              >登录</a>
              <span style="margin: 0 5px">|</span>
              <a :class="[type ===2?'active':'']"
              @click="changeType(2)"
              >注册</a>
            </h1>
          <div class="login-form">
              <div style="margin-bottom:10px;">
                  <input
                  v-model="form.user_id"
                  type="text"
                  placeholder="请输入账号">
              </div>
              <div style="margin-bottom:10px;">
                  <input
                  v-model="form.user_pwd"
                  type="password"
                  placeholder="请输入密码">
              </div>
              <div style="margin-bottom:10px;" v-if="type===2">
                  <input
                  v-model="form.re_user_pwd"
                  type="password"
                  placeholder="请再次确认密码">
              </div>
              <div style="margin-bottom:10px;display:flex;">
                  <input
                  v-model="form.code"
                  type="text"
                  style="flex:1"
                  placeholder="请输入验证码">
                  <img
                  @click="getVeri()"
                  style="width:100px;height:40px;cursor:pointer" :src="code.img" alt="">
              </div>
              <div style="margin-bottom:10px;">
                  <button @click="Submit()">{{type===1?'登录':'注册'}}</button>
                </div>
          </div>
      </div>
  </div>
</transition>
</template>

<script>
import { getVerification, SignIn } from '../api/index'
import { mapActions } from 'vuex'
export default {
    name:'login-modal',
    data(){
        return{
            show:false,
            type:1,
            code: {
                token:'',
                img: '',
            },
            form:{
                user_name: '',
                user_id: '',
                user_pwd: '',
                re_user_pwd: '',
                code: '',
            }
        }
    },
    methods:{
        ...mapActions('login',['Change']),
        getVeri(){
            getVerification()
            .then(
                res=>{
                    if (res.code === 200) {
                        this.code = res.data
                    }
                }
            )
        },
        changeType(val){
            let form = this.form;
            let reset = {
                user_name: '',
                user_id: '',
                user_pwd: '',
                re_user_pwd: '',
                code: '',
            };
            this.type = val;
            this.form = { ...form, ...reset }
        },
        Submit(){
            if (this.type === 1){
                this.Login()
                return
            }
            if (this.type === 2){
                this.Regist()
                return
            }
        },
        Login(){
            let { user_id, user_pwd, code } = this.form;
            let code_token = this.code.token;
            SignIn({user_id, user_pwd, code, code_token})
            .then(res => {
                if (res.code === 200) {
                    this.$add('pins-user',res.data);
                    this.Change(true)
                    this.show = false
                }
            })
        },
        Regist(){
            let { user_id, user_pwd,re_user_pwd, code } = this.form;
            let code_token = this.code.token;
            let user_name = `CPDD_${Math.random().toString(34).substring(2,8).toUpperCase()}`
            console.log({
                user_id, user_pwd,re_user_pwd, code,code_token,user_name
            })
        }
    },
    created(){
        this.event.$on('sign-in',function(){
            this.show = true;
            this.getVeri();
        }.bind(this))
    }
}
</script>

<style lang="stylus" scoped>
.login
    width 100%
    min-width 960px
    position fixed
    top 0px
    left 0px
    height 100vh
    background-color rgba(0,0,0,0.4)
    z-index 99
    &-modal
        position absolute
        top 50%
        left 50%
        transform translate(-50%,-50%)
        background-color #fff
        width 350px
        min-height 250px
        border-radius: .2rem;
        box-shadow: 0 1px 2px 0 rgba(0,0,0,.05);
        padding 30px 15px 15px 15px
        .active 
            color #007fff
            font-weight bold
        &_close
            cursor: pointer;
            width: 17px;
            height: 17px;
            position: absolute;
            top: 6px;
            right: 6px;
            transform: rotate(45deg);
            &:before,&:after
                content: "";
                width: 13px;
                height: 1px;
                display: block;
                position: absolute;
                background: #000;
                left: 50%;
                top: 50%;
                transform: translate(-50%,-50%);
            &:after 
                transform: translate(-50%,-50%) rotate(90deg);
    &-form 
        padding 30px
.fade-enter-active,.fade-leave-active
    transition: opacity 200ms linear
.fade-enter,.fade-leave-to
    opacity 0 
</style>