<template>
  <div id="app" class="container">
    <login-modal />
    <div class="nav">nav</div>
    <div class="main"><router-view/></div>
    <div class="aside">aside</div>
  </div>
</template>
<script>
  import loginModal from './components/loginModal'
  import { mapActions } from 'vuex'
  export default {
    name:'app',
    components:{
      loginModal
    },
    methods:{
      ...mapActions('login',['Change'])
    },
    created(){
      if (this.$check('pins-user')){
        this.Change(true)
      }else{
        this.Change(false)
      }
      this.event.$on('changeLogin',function(res){
        this.Change(res)
      }.bind(this))
    }
  }
</script>
