import axios from "axios";
import qs from 'qs';
import { add, get, remove} from './storage'
import event from '../ev'
let BASEURL = "http://132.232.89.204:2335";
const service  = axios.create({
    baseURL:BASEURL,
    timeout:0
});
const delKey = (data) => {
    Object.keys(data).forEach(key=>{
        if(data[key]===null||data[key]===''){
            delete data[key]
        }
    })
    return data 
};
service.interceptors.request.use(
    conf => {
        let token = get('pins-user')?get('pins-user').token:null;
        conf.method === 'get'?
        conf.params = conf.params:
        conf.data = qs.stringify(conf.data);
        conf.headers['Content-Type'] = 'application/x-www-form-urlencoded';
        if (token) {
            conf.headers['Authorization'] = token
        }
        return conf
    },
    err => {
        Promise.reject(err)
    }
)

service.interceptors.response.use(
    res => {
        if (res.data.code === 401){
            event.$emit('changeLogin',false)
            remove('pins-user')
        }
        return res.data
    },
    err => {
        return Promise.reject(err)
    }
)

export default service