    export const check= (a) => {
        if (window.localStorage.getItem(a)) {
            return true
        } else {
            return false
        }
    }
    //添加Storage
    export const add=(key, value) => {
        window.localStorage.setItem(key, JSON.stringify({data:value}))
    }
    //获取Storage
    export const get= (key) => {
        let result = window.localStorage.getItem(key)&&JSON.parse(window.localStorage.getItem(key)).data;
        return result
    }
    export const sessionAdd= (key, value) => {
        window.sessionStorage.setItem(key, JSON.stringify({data:value}))
    }
    //获取Storage
    export const sessionGet= (key) => {
        let result = window.sessionStorage.getItem(key)&&JSON.parse(window.sessionStorage.getItem(key)).data;
        return result
    }
    //删除Storage
    export const remove= (key) => {
        window.localStorage.removeItem(key)
    }
