import service from '../utils/http'
import axios from 'axios'

export const upload = file => {
    const formData = new FormData();
    formData.append('image', file);
    return axios({
        method: 'post',
        url: 'http://132.232.89.204:2335//api/upload-single',
        data:formData
    })
}

export const getUrlParse = url => {
    return service({
        method:'post',
        url:'/api/url/parse',
        data:{
            url:url
        }
    })
}

export const getVerification = () => {
    return service({
        method:'get',
        url:'/api/code/checkcode',
    })
}

export const SignIn = data => {
    return service({
        method: 'post',
        url:'/api/user/login',
        data,
    })
}
export const addCommit = data => {
    return service({
        method: 'post',
        url: '/api/artical/add',
        data
    })
}

export const getArtical = page => {
    return service({
        method: 'get',
        url: 'api/artical/get',
        params: {
            page:page
        }
    })
}